import React from 'react'
import { Link } from 'react-router-dom'

const CategoryCard = (props) => {
  
  let url = 'https://image.tmdb.org/t/p/w500';
  let path = props.pp;
  let imgurl = url + path;
  let direction = `/${props.mtd}/${props.id}`
  
  let show = props.display ? 
  <div className="result-rating">
    <div className="star"><svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="star" className="svg-inline--fa fa-star fa-w-18" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512"><path fill="currentColor" d="M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z"></path></svg></div>
    <p className="rating">{props.rating}</p>
  </div> : '';
  
  return (
    <React.Fragment>
      <Link to={direction} id={props.id} className="result">
        <div className="img-container">
          <img src={imgurl} alt="" className="result-img" />
          {show}
        </div>
        <div className="result-name">{props.name}</div>
      </Link>
    </React.Fragment>
  )
}

export default CategoryCard